.aboutContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 4em;
  height: fit-content;
}

.title {
  color: black;
  font-size: 2em;
  font-weight: 450;
  margin: 1em 0em;
}

.aboutInfoContainer {
  margin: 3em 0em;
  padding: 2em 3em;
  height: fit-content;
  width: 50%;
  border-radius: 1em;
  overflow: hidden;
  background-color: rgb(244, 245, 247);
  border: 1px solid rgba(164, 164, 164, 0.5);
  box-shadow: 0.01em 0.01em 1em 0.01em rgba(0, 0, 0, 0.15);

  display:flex;
  text-align: left;
  flex-direction: column;
}

